@use 'base/variables' as v;
@use 'base/mixins' as m;


.encabezado-factura{

    display: grid;
    
    //grid-template-columns: repeat(5, 1fr);
    grid-template-columns: 4fr 1fr;
    justify-content: space-between;
    min-height: 10vh;

}

.encabezado-cliente{    
    inline-size: fit-content;
}

.tabla-factura{
    width: 100%;
    border: 1px solid rgb(237, 240, 246);

    th, td {
        text-align: center;
        width: 25%;
        //text-align: left;
        vertical-align: top;
        border: 1px solid rgb(237, 240, 246);
     }
}

