@use 'base/variables' as v;
@use 'base/mixins' as m;

.cabecero-ventas{
    align-items: center;
    @include m.tablet{
        display: flex;
        justify-content: space-between;
        margin: 0 3rem;
        padding: 0 1rem;
    }
}

.cabecero-ventas_tabla{   
   
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        align-items: end;
        justify-content: space-between;
        margin: 1rem 3rem;
        padding: 0 1rem;
    
}

.buscar-ventas{
    background-color: aquamarine;
}